html, body, #root {
    height: 100%;
}

header {
    height: 80px;
    display: flex;
}

main {
    display: flex;

}

.possible-steps {
    width: fit-content;
    display: flex;
}

.possible-step {
    width: 72px;
    height: 72px;
}
.left-section {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.step {
    width: 200px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
}

.player-action-panel {
    display: flex;
    justify-content: space-between;
    width: 202px;
}

.player-action {
    font-size: 50px;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.focus {
    margin-top: 143px;
    display: flex;
    position: relative;
}
.focus:before {
    content: 'Фокус';
    position: absolute;
    left: 281px;
    top: -31px;
}

.basket {
    text-align: center;
}